import Vue from 'vue';
import Vuex from 'vuex';
import state from './states';
import mutations from './mutations';

//  vue  vuex
Vue.use(Vuex);
//  new Vuex.Store  state  mutations store
//  store  vue 
export default new Vuex.Store({
  state,
  mutations
})



